<template>
  <div class="home">
    <el-container style="height: 100%">
      <el-aside class="aside" width="300px">
        <div class="search">
          <el-input
            class="ipt"
            v-model="identifier"
            placeholder="请输入identifier,如G0101"
          ></el-input>
          <el-button
            class="btn"
            type="primary"
            icon="el-icon-search"
            @click="search"
            >搜索</el-button
          >
        </div>
        <div class="menu">
          <el-menu
            :default-openeds="keyPath"
            @open="handleOpen"
            @close="handleClose"
            :unique-opened="true"
          >
            <!-- 1 -->
            <el-submenu
              :index="String(one.category.id)"
              v-for="one in categoryList"
              :key="one.category.identifier"
            >
              <template slot="title">{{
                one.category.identifier + "_" + one.category.title
              }}</template>
              <el-submenu
                :index="String(two.category.id)"
                v-for="two in one.sonCategory"
                :key="two.category.identifier"
              >
                <template slot="title">{{
                  two.category.identifier + "_" + two.category.title
                }}</template>
                <el-submenu
                  :index="String(three.category.id)"
                  v-for="three in two.sonCategory"
                  :key="three.category.identifier"
                >
                  <template slot="title">{{
                    three.category.identifier + "_" + three.category.title
                  }}</template>
                  <el-menu-item
                    :index="String(four.category.id)"
                    v-for="four in three.sonCategory"
                    :key="four.category.identifier"
                    >{{
                      four.category.identifier + "_" + four.category.title
                    }}</el-menu-item
                  >
                </el-submenu>
              </el-submenu>
            </el-submenu>
          </el-menu>
        </div>
        <div class="mapping">
          <router-link to="/search">匹配（跳转）</router-link>
        </div>
      </el-aside>
      <el-main>
        <div class="tableHeader">
          {{ tabName || "表名" }}
        </div>
        <el-table
          :data="tableData"
          stripe
          v-loading="loading"
          height="550"
          border
          style="width: 100%"
        >
          <af-table-column prop="id" label="序号"></af-table-column>
          <af-table-column prop="englishName" label="英文名称" width="180">
          </af-table-column>
          <af-table-column prop="chineseName" label="中文名称" width="180">
          </af-table-column>
          <af-table-column prop="description" label="字段描述">
          </af-table-column>
          <af-table-column prop="unit" label="单位"> </af-table-column>
          <af-table-column prop="typePrecision" label="类型与精度">
          </af-table-column>
          <af-table-column prop="rangeConstraint" label="值域/约束">
          </af-table-column>
          <!-- <el-table-column prop="address" label="表名"> </el-table-column> -->
        </el-table>
        <div class="block">
          <!--  @size-change="handleSizeChange" -->
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  components: {},
  data() {
    return {
      identifier: "", // 侧边栏搜索内容
      categoryList: [], // 侧边栏目录
      keyPath: [], // 侧边栏路径
      tableData: [], // 显示当前表数据
      tabName: "", // 表名
      categoryId: null,
      pageSize: 100,
      total: 100,
      loading: false,
      currentPage: 1,
    };
  },
  created() {
    this.getCategoryList();
  },
  methods: {
    // 获取目录列表
    getCategoryList() {
      this.$axios({
        method: "get",
        url: "/pipeline_data_sys/category/list",
      })
        .then((res) => {
          this.categoryList = res.data;
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
    // 侧边栏搜索
    search() {
      // 为空提示
      if (this.identifier == "") {
        this.$message({
          type: "warning",
          message: "请输入identifier",
          duration: 3000,
          showClose: true,
        });
      } else {
        // 请求路径
        this.$axios({
          url: `/pipeline_data_sys/category/${this.identifier}/search`,
        })
          .then((res) => {
            // 路径为空，
            if (res.data.length == 0) {
              this.$message({
                type: "warning",
                message: "请输入正确的identifier",
                duration: 3000,
                showClose: true,
              });
            } else {
              this.keyPath = res.data;
              this.categoryId = this.keyPath[this.keyPath.length - 1];
              this.handleOpen(this.categoryId, this.keyPath);
              this.$forceUpdate();
            }
          })
          .catch((err) => {
            this.$message({
              type: "error",
              message: "网络出错了",
              duration: 3000,
              showClose: true,
            });
          });
      }
    },
    handleOpen(key, keyPath) {
      this.categoryId = key;
      this.keyPath = keyPath;
      // 拿categoryId请求表格数据
      this.getTableData(this.categoryId);
    },
    // 请求表格数据
    getTableData(categoryId) {
      this.loading = true;
      this.$axios({
        method: "get",
        url: `/pipeline_data_sys/attribute/${categoryId}`,
      })
        .then((res) => {
          setTimeout(() => {
            this.loading = false;
          }, 400);
          this.total = 100;
          if (res.data.length != 0) {
            this.tableData = res.data.attributes;
            this.tabName = this.tableData[0].tabName;
            this.total = res.data.total * 10;
          } else {
            this.tableData = [];
          }
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
    handleClose() {},
    handleCurrentChange(val) {
      this.loading = true;
      this.$axios({
        url: `/pipeline_data_sys/attribute/${this.categoryId}`,
        params: {
          pageNum: val,
        },
      })
        .then((res) => {
          setTimeout(() => {
            this.loading = false;
          }, 400);
          this.tableData = res.data.attributes;
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
  },
};
</script>

