<template>
  <div class="search">
    <div class="mapping">
      <el-select class="select" v-model="colName" placeholder="请选择">
        <el-option
          v-for="item in colsList"
          :key="item.col"
          :label="item.col"
          :value="item.name"
        >
        </el-option>
      </el-select>
      <el-input
        class="ipt"
        v-model="colValue"
        placeholder="请输入内容"
      ></el-input>
      <el-button type="primary" @click="search">匹配</el-button>
    </div>
    <div class="dataShow">
      <el-table
        :data="tableData"
        v-loading="loading"
        height="500"
        border
        stripe
        style="width: 100%"
      >
        <af-table-column prop="id" label="id"></af-table-column>
        <af-table-column prop="title" label="title"></af-table-column>
        <af-table-column prop="identifier" label="identifier">
        </af-table-column>
        <af-table-column prop="englishName" label="英文名称" width="180">
        </af-table-column>
        <af-table-column prop="chineseName" label="中文名称" width="180">
        </af-table-column>
        <af-table-column prop="description" label="字段描述"> </af-table-column>
        <af-table-column prop="unit" label="单位"> </af-table-column>
        <af-table-column prop="typePrecision" label="类型与精度">
        </af-table-column>
        <af-table-column prop="rangeConstraint" label="值域/约束">
        </af-table-column>
      </el-table>
      <div class="block">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      colsList: [],
      colName: "",
      colValue: "",
      tableData: [],
      loading: false,
      currentPage: 1,
      pageSize: 100,
      total: 100,
    };
  },
  components: {},
  computed: {},
  created() {
    this.getColsList();
  },
  methods: {
    getColsList() {
      this.$axios({
        url: "/pipeline_data_sys/attribute/cols",
      })
        .then((res) => {
          console.log("colsList", res);
          this.colsList = res.data;
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
    search() {
      if (this.colName == "") {
        this.$message({
          type: "error",
          message: "请选择colName",
          duration: 3000,
          showClose: true,
        });
      } else if (this.colValue == "") {
        this.$message({
          type: "error",
          message: "请输入colValue",
          duration: 3000,
          showClose: true,
        });
      } else {
        this.loading = true;
        this.$axios({
          url: `/pipeline_data_sys/attribute/${this.colName}/search/${this.colValue}`,
        })
          .then((res) => {
            this.tableData = [];
            this.total = res.data[0].total * 10;
            res.data.forEach((element) => {
              element.attributes.forEach((item) => {
                item.title = element.title;
                item.identifier = element.identifier;
              });
              this.tableData = this.tableData.concat(element.attributes);
            });
            setTimeout(() => {
              this.loading = false;
            }, 400);
          })
          .catch((err) => {
            this.$message({
              type: "error",
              message: "网络出错了",
              duration: 3000,
              showClose: true,
            });
          });
      }
    },
    handleCurrentChange(val) {
      this.loading = true;
      this.$axios({
        url: `/pipeline_data_sys/attribute/${this.colName}/search/${this.colValue}`,
        params: {
          pageNum: val,
        },
      })
        .then((res) => {
          this.tableData = [];
          res.data.forEach((element) => {
            element.attributes.forEach((item) => {
              item.title = element.title;
              item.identifier = element.identifier;
            });
            this.tableData = this.tableData.concat(element.attributes);
          });
          setTimeout(() => {
            this.loading = false;
          }, 400);
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
  },
};
</script>

<style lang='scss' scoped>
.search {
  box-sizing: border-box;
  height: calc(100% - 60px);
  padding: 10px 20px;
  .mapping {
    display: flex;
    justify-content: start;
    align-items: center;
    height: 100px;
    .select {
      width: 200px;
      margin-right: 20px;
    }
    .ipt {
      width: 300px;
      margin-right: 20px;
    }
  }
  .dataShow {
    position: relative;
    height: calc(100% - 100px);
    /deep/ .el-table__header {
      height: 80px;
      background-color: #e3e5e9;
    }
    .block {
      position: absolute;
      right: 20px;
      bottom: 10px;
    }
  }
}
</style>